import React, { useState, useEffect } from "react";
import image1 from "./images/kin13.JPG";
import image2 from "./images/kin14.JPG";
import image3 from "./images/kin15.JPG";


const HeroSlider = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const images = [image1, image2, image3];
  // const heroTexts = ["Pozostań bezpieczny", "Twoje bezpieczeństwo jest dla nas najważniejsze", "Zapobieganie jest kluczem"];
  const heroSubTexts = [
    "Nasze szkolenia BHP to klucz do minimalizacji ryzyka wypadków i zapewnienia przestrzegania przepisów.",
    "Bezpieczeństwo pracowników to nasza misja! Wspólnie dbamy o ich pewność w miejscu pracy.",
    "Zapewniamy najwyższej jakości urządzenia gaśnicze."
  ];

  // set automatic slide interval
  useEffect(() => {
    const intervalId = setInterval(goToNextSlide, 5000); // change delay time here (in milliseconds)
    return () => clearInterval(intervalId);
  }, [currentIndex]);

  const goToPrevSlide = () => {
    const lastIndex = images.length - 1;
    const shouldResetIndex = currentIndex === 0;
    const index = shouldResetIndex ? lastIndex : currentIndex - 1;
    setCurrentIndex(index);
  };

  const goToNextSlide = () => {
    const lastIndex = images.length - 1;
    const shouldResetIndex = currentIndex === lastIndex;
    const index = shouldResetIndex ? 0 : currentIndex + 1;
    setCurrentIndex(index);
  };

  const isActiveSlide = (index) => {
    return currentIndex === index;
  };

  return (
    <div
      className={`hero-image ${isActiveSlide(currentIndex) ? "active" : ""}`}
      style={{
        backgroundImage: `url('${images[currentIndex]}')`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        position: "relative"
      }}
      onClick={goToNextSlide}
    >
      <div
        className="hero-overlay"
        // style={{ backgroundColor: "rgba(55, 144, 220, 0.5)" }}
           style={{ backgroundColor: "rgba(107.142.165.0.5" }}
          //  style={{ backgroundColor: "rgba(50,50,50,0.8)" }}
          // style={{ backgroundColor: "rgba(139,0,0,0.7)" }}
      ></div>
      <div
        className="hero-container"
        style={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          zIndex: 1
        }}
      >
        {/* <h1 className="hero-text">{heroTexts[currentIndex]}</h1> */}
        <p className="hero-subtext">{heroSubTexts[currentIndex]}</p>
      </div>
      <button className="slider-prev" onClick={goToPrevSlide}>
        &lt;
      </button>
      <button className="slider-next" onClick={goToNextSlide}>
        &gt;
      </button>
    </div>
  );
};

export default HeroSlider;